let scrollAnimate = () => {
  let targets
  let windowHeight
  let init = () => {
    targets = document.getElementsByClassName("hidden");
    windowHeight = window.innerHeight;
    _addEventHandlers();
  }
  let _addEventHandlers = () => {
    window.addEventListener("scroll", _checkPosition);
    window.addEventListener("resize", init)
  }
  let _checkPosition = () => {
    for (let i = 0; i < targets.length; i++) {
      let posFromTop = targets[i].getBoundingClientRect().top;
      if (posFromTop - windowHeight <= 0) {
        targets[i].className = targets[i].className.replace("hidden", "fade-in");
      }
    }
  }
  return {
    init: init
  }
}
scrollAnimate().init();
